import React from 'react';
import NavMob from '../components/NavMob';

import '../styles/_portraitReady.scss';

const processCookies = () => {
  if(document.cookie.length > 0){
    let decodedCookie = decodeURIComponent(document.cookie);
    let cookieParts = decodedCookie.split(';');

    for(let i = 0; i < cookieParts.length; i++){
      let cookieName = cookieParts[i].split('=');

      if(cookieName[0].replace(/\s/g, '') === "dataAW"){
        let dataSplit = cookieName[1].split(",");

        let jpgLink = `https://s3-ap-southeast-2.amazonaws.com/pepeha-dev/uploads/${dataSplit[0]}/image/${dataSplit[1]}.jpg`,
            shopLink = "https://shop.pepeha.nz/products/a3-print?pepehaId=" + cookieName[1],
            pdfLink = "https://shop.pepeha.nz/products/digital-file?pepehaId=" + cookieName[1];

        return { jpgLink, shopLink, pdfLink }
      }
    }
  } else {
    return false
  }
}

const PortraitReady = () => {
  const links = processCookies();

  // previosuly .thanks
  return(
    <main className={`portraitReady`}>
      <NavMob/>
      
      <section className="portraitReady-top">
        <div className="portraitReady-content">
          <a href={ links.jpgLink } download="pepeha.jpg" target="_blank" rel="noopener noreferrer" className="portraitReady-button -ios">DOWNLOAD FREE JPEG</a>
          <p className="portraitReady-paragraph -para1">Keep this handy to practise your Pepeha with your friends and wh&#x101;nau.</p>  
        </div>
      </section>

      <section className="portraitReady-middle">
        <div className="portraitReady-content">
          <a href={ links.pdfLink } target="_blank" rel="noopener noreferrer" className="portraitReady-button -pdf">High-res PDF - $20</a>
          <p className="portraitReady-paragraph -para1">A high-res digital PDF download so you can print and manage yourself.</p>  
        </div>
      </section>

      <section className="portraitReady-bottom">
        <div className="portraitReady-content">
          <a href={ links.shopLink } target="_blank" rel="noopener noreferrer" className="portraitReady-button -print">PRINTED - $40/$100</a>
          <p className="portraitReady-paragraph -para1">Printed or framed A3 Pepeha to display in your home or as a gift for friends and wh&#x101;nau.</p> 
        </div>
      </section>
    </main>
  )
}

export default PortraitReady;